.company-reportrow {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px 15px;;
    border-radius: 4px;
    border-bottom: 1px solid var(--mainBlue);
    background-color: var(--mainWhite);

    &-container {
        width: 100%;
    }

    &-details {
        width: 100%;
        display: flex;
        justify-content: space-around;

        &-field {
            width: 25%;
            display: flex;
            justify-content: center;
        }
    }

    & p {
        color: var(--mainBlue);
    }

    &-toggle {
        width: 100%;
        display: flex;
        justify-content: flex-end;
    }
}

.company-report-download-box {
    width: 25px;
    height: 25px;

    & svg {
        width: 25px;
        height: 25px;
        color: var(--mainBurgund);

        &:hover {
            color: var(--mainGreen);

        }
    }
}

